.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.fs.error-main{
 background-color:rgb(222,23,56)
 
}

.fs-products-table td img{
  max-width:200px !important;
  max-height:250px !important
}

.fs-navbar-header{
  background-color: #282c34;
  color: white !important;
  padding-top:25px;
  font-size:1.9em
}
.fs-navbar-buttons{

  background-color: #282c34;
  color: white;
  padding-bottom:10px;
}

.fs-navbar-buttons .fs-useraction-section{

  float:left;
  padding-bottom:50px
}

.fs-main-content{
  background: rgb(2,0,36);
  background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,121,29,1) 35%, rgba(0,212,255,1) 100%);
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
